.contContBotones {
  display: flex;
  justify-content: center;
}
.contBotones {
  margin: 0px 4px 0px 4px;
}
.estiloBtnDifAnim {
  height: 60px;
  margin: 6px 4px 6px 4px !important;
  padding: 3px 6px 3px 6px !important;
  color: rgb(141, 255, 255) !important;
  background-color: rgb(0, 19, 27) !important;
  border: 3px solid rgb(141, 255, 255) !important;
  border-radius: 8px !important;
  -webkit-border-radius: 8px !important;
  -moz-border-radius: 8px !important;
  -ms-border-radius: 8px !important;
  -o-border-radius: 8px !important;
  cursor: pointer;
  text-transform: none !important;
}
.paddingUl {
  padding-left: 1rem;
}
.tamañoLi {
  font-size: 15px;
}

.tituloAlerVanilla {
  text-align: center;
  background-color: rgb(20, 70, 70);
}

.tituloAlerMUI {
  text-align: center;
  background-color: rgb(63, 97, 97);
}

.cuerpoAnim {
  height: 700px;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: rgb(73, 121, 121);
}

.contContCont {
  display: flex;
  justify-content: center;
  align-items: center;
}

.contBtnDifAnim {
  height: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  background-color: rgb(15, 99, 99);
}

.contAnimIzq {
  background-color: rgb(16, 54, 54);
}
.contAlertaVanilla {
  display: flex;
  justify-content: center;
  align-items: center;
}
.alertaVanilla {
  border: 3px solid rgb(168, 105, 23);
  border-radius: 10px;
  background-color: rgb(44, 179, 179);
  text-align: center;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  -o-border-radius: 10px;
}
.alerVaCorr {
  border: 3px solid rgb(44, 145, 49);
}
.alerVaInco {
  border: 3px solid rgb(165, 40, 40);
}

.contAnimCen {
  background-color: rgb(46, 107, 107);
}

.contAnimDer {
  background-color: rgb(25, 160, 160);
}

.AlertaAnimVanilla {
  height: 50px;
  width: 300px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgb(4, 126, 89);
  color: rgb(0, 53, 37);
  border: 5px solid rgb(0, 53, 37);
  border-radius: 10px;
  position: absolute;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  -o-border-radius: 10px;
}

.anim {
  animation-name: AlVani;
  animation-duration: 3s;
}
@keyframes AlVani {
  0% {left: -300px; top: 0px;}
  30% {left: 0px; top: 0px;}
  80% {left: 0px; top: 0px;}
  100% {left: -300px; top: 0px;}
}

